import { Injectable } from '@angular/core';

import { Metal, METALS } from './metal'; 
import { Shape, SHAPES } from './shape';

@Injectable()
export class CalculatorService {
    getShapes(): Shape[] {
        return SHAPES;
    }

    getMetals(): Metal[] {
        return METALS;
    }
}


