import { Component, Input, OnChanges } from '@angular/core';
import { OnInit } from '@angular/core';
import { AbstractControl, FormArray, FormGroup, FormControl, FormBuilder, Validators, ValidatorFn } from '@angular/forms';

import { Density } from './density'
import { Metal } from './metal'
import { Shape } from './shape'
import { Parameter } from './parameter'
import { CalculatorService } from './calculator.service'

function canParseNumber(control: AbstractControl): {[key: string]: any} {
  let value = AppComponent.parseNumber(control.value);
  
  if(isNaN(value)) {
     return {'not_a_number': value};
  }
  
  return null;
}

function isNonNegativeNumber(control: AbstractControl): {[key: string]: any} {
    let value = AppComponent.parseNumber(control.value);
    
    if(isNaN(value) || value < 0.0) {
      return {'negative_number': value};
    }
    
    return null;
}

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent implements OnInit {
  title = 'Kalkulator Metali Kolorowych';
  calculatorForm: FormGroup;

  rate: number;
  totalValue: string;
  selectedShape: Shape;
  selectedMetal: Metal;
  shapes: Shape[];
  metals: Metal[];

  formErrors = {'rate': []};
  parameterErrors = {};

  static parseNumber(value: string): number {
    if (typeof value === 'number') {
      return value;
    }
    if (typeof value === 'string') {
      let valueToUse = value.replace(/,/, '.');
      return Number(valueToUse);
    }
    return value;
  }

  getParameterClass(index: number): string {
    let classToUse = 'input-group';
    
    const selectedParameter = this.selectedShape.parameters[index];
    const errors = this.parameterErrors[selectedParameter.name];
    if(errors.length > 0) {
      classToUse += ' has-error';
    }

    return classToUse;
  }

  constructor(private calculatorService: CalculatorService, private formBuilder: FormBuilder) { }

  ngOnInit(): void {
    this.shapes = this.calculatorService.getShapes();
    this.metals = this.calculatorService.getMetals();

    this.rate = 0.0;
    this.totalValue = '0,00';
    this.selectedShape = this.shapes[0];
    this.selectedMetal = this.metals[0];

    this.createForm();
  }

  get shapeControl(): FormControl {
    return this.calculatorForm.get('selectedShape') as FormControl;
  }

  get parametersControl(): FormArray {
    return this.calculatorForm.get('parametersControl') as FormArray;
  };

  get metalControl(): FormControl {
    return this.calculatorForm.get('selectedMetal') as FormControl;
  }

  get rateControl(): FormControl {
    return this.calculatorForm.get('rate') as FormControl;
  }

  get densityControl(): FormControl {
    return this.calculatorForm.get('selectedDensity') as FormControl;
  }

  onValueChanged(data?: any): void {
    if (!this.calculatorForm) { return; }
    
    const form = this.calculatorForm;

    for (const field in this.formErrors) {
      // clear previous error message (if any)
      this.formErrors[field] = [];
      const control = form.get(field);
      if (control && control.dirty && !control.valid) {
        for (const key in control.errors) {
          this.formErrors[field].push(key);
        }
      }
    }

    let index = 0;
    for (const parameter in this.parameterErrors) {
      // clear previous error message (if any)
      this.parameterErrors[parameter] = [];
      const control = form.get('parametersControl.' + index + '.value');
      if (control && control.dirty && !control.valid) {
        for (const key in control.errors) {
          this.parameterErrors[parameter].push(key);
        }
      }
      index += 1;
    }
  }

  createForm() {
    this.calculatorForm = this.formBuilder.group({
      selectedShape: [ this.selectedShape, Validators.required ],
      selectedMetal: [ this.selectedMetal, Validators.required ],
      selectedDensity: [ this.selectedMetal.defaultDensity, Validators.required ],
      rate: [ this.rate, Validators.required ],
      totalValue: 0.0,
      parameters: this.formBuilder.array([])
    });
    this.setParameters(this.selectedShape.parameters);

    this.shapeControl.valueChanges.forEach(
      (shape: Shape) => {
        this.selectedShape = shape;
        this.setParameters(shape.parameters);
      });
    
    this.metalControl.valueChanges.forEach(
      (metal: Metal) => {
        this.selectedMetal = metal;
        this.calculatorForm.patchValue({ selectedDensity: this.selectedMetal.densities[0] });
      });

    this.rateControl.valueChanges.forEach((value: any) => this.updateTotalValue());
    this.densityControl.valueChanges.forEach((value: Density) => this.updateTotalValue());
    this.calculatorForm.valueChanges.subscribe(data => this.onValueChanged(data));
    this.onValueChanged();
  }

  setParameters(parameters: Parameter[]) {
    this.parameterErrors = {};
    parameters.forEach(parameter => {
      this.parameterErrors[parameter.name] = [];
    });

    const parameterGroups = parameters.map(parameter => this.formBuilder.group(
      { value: [parameter.defaultValue, [Validators.required, canParseNumber, isNonNegativeNumber]] }));
    const parameterFormArray = this.formBuilder.array(parameterGroups);
    this.calculatorForm.setControl('parametersControl', parameterFormArray);

    this.parametersControl.valueChanges.forEach((value: any) => this.updateTotalValue());
  }

  updateTotalValue(): void {
    let parameterValues: number[] = this.parametersControl.value.map((parameter: any) => AppComponent.parseNumber(parameter.value));
    let volume = this.selectedShape.getVolume(parameterValues);
    let density = this.densityControl.value.value;
    let rate = AppComponent.parseNumber(this.rateControl.value);
    
    let rawValue = rate * density * volume;
    let roundFactor = 100.0;
    let roundedValue = Math.round(rawValue * roundFactor) / roundFactor;

    let totalValueToUse: string;
    if(rawValue == 0.0 || isNaN(rawValue)) {
      this.totalValue = '0,00';
    } else if(rawValue > 0.0 && roundedValue < 0.01) {
      this.totalValue = '> 0,01';
    } else {
      this.totalValue = roundedValue.toFixed(2);
      this.totalValue = this.totalValue.replace('\.', ',');
    }

    this.calculatorForm.patchValue({ totalValue: totalValueToUse });
  }
}



